* {
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', 'Circe', 'CirceBold', sans-serif;
    text-decoration: none;
    /* background-color: var(--primary-background-color); */
    margin: 0;
}

h1,
h2,
h3,
h4 {
    margin: 0px;
}

ul {
    margin: 0px;
    padding: 0px;
}

a {
    text-decoration: none;
    cursor: pointer;
    margin: 0px;
    padding: 0px;
}

button {
    text-decoration: none;
    cursor: pointer;
    margin: 0px;
    padding: 0px;
    border: none;
    background: none;
}

p {
    margin: 0px;
}

li {
    list-style: none;
    margin: 0;
    padding: 0;
}


.is-hidden {
    display: none;
}


